body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.page-custom-active-link {
    background-color: #795548 !important;
    border-color:#4E342E !important;
    
}

.page-custom-link {
    color:#4E342E !important;
}

.page-custom-link:focus {
    box-shadow: 0 0 0 .2rem rgba(62, 39, 35, 0.493);
}
.search-form {
  padding: 10px;
}

.search-container {
  margin-top: 20px;
}

.book-tags {
  color: #a1887f;
  font-size: 14pt;
  padding-top: 5px;
}

.card-book-info {
  padding-left: 0 !important;
}


.main-container {
  padding-top: 20px;
  margin-bottom: 100px;
}
.card-book {
  margin-bottom: 10px;
}
.card-book-preview {
  padding-left: 0 !important;
}

.book-title {
  color: #000;
}
.book-title:hover {
  color: #795548;
  text-decoration: none;
}
.book-title {
  margin-bottom: 20px;
}
.preview-column {
  background: #ffcdd2;
}
.back-row {
  padding-top: 20px;
  padding-bottom: 20px;
}

.back-link:hover {
  text-decoration: none;
}

.custom-link {
  color:rgb(104, 72, 65);
}

.custom-link:hover {
  color:rgb(48, 31, 28);
}
.header {
  background: #d7ccc8;
}
.header-link a:hover {
  text-decoration: none;
}

.footer {
  background: #d7ccc8;
}

.button-custom {
  background-color: #795548;
  border-color:#4E342E;
}

.button-custom:hover {
  background-color: #3E2723;
  border-color:#4E342E;
}

.button-custom:active {
  background-color: #3E2723 !important;
  border-color:#4E342E !important;
  box-shadow: 0 0 0 .2rem rgba(62, 39, 35, 0.493);
}

.button-custom:focus {
  box-shadow: 0 0 0 .2rem rgba(62, 39, 35, 0.493) !important;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

